/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#primary[tabindex="-1"]:focus {
  outline: 0;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.site {
  margin: 0 auto;
  max-width: 100%;
}

@media (--sidebar-query) {
  .has-sidebar .site {
    margin: 0 1em;
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-gap: 2rem;
    justify-content: center;
  }

  .site-header {
    grid-column: span 2;
  }

  .site-main {
    grid-column: 1;
    min-width: 0;
  }

  .primary-sidebar {
    grid-column: 2;
    min-width: 0;
  }

  .site-footer {
    grid-column: span 2;
  }
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
  color: var(--global-font-color);
  font-family: var(--global-font-family);
  font-size: var(--global-font-size)px;
  font-size: calc(var(--global-font-size) / 16)rem;
  line-height: var(--global-font-line-height);
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--highlight-font-family);
  color: #32373c;
  clear: both;
}

.entry-header h1.entry-title {
  font-size: 32px;
  font-size: 2rem;
  line-height: 1.4;
  margin: 1em 0;
}

dfn, cite, em, i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5em;
}

address {
  margin: 0 0 1.5em;
}

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: 15px;
  font-size: 0.9375rem;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em;
}

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: 12.8px;
  font-size: .8em;
}

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark, ins {
  background: #fff9c0;
  text-decoration: none;
}

big {
  font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background: #fff;
  /* Fallback for when there is no custom background color defined. */ }

blockquote, q {
  quotes: "" "";
}

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em;
}

ul, ol {
  margin: 0 0 1.5em 3em;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 1.5em 1.5em;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

figure {
  /* Extra wide images within figure tags don't overflow the content area. */
  margin: .5em 0;
}

table {
  margin: 0 0 1.5em;
  width: 100%;
}

/*--------------------------------------------------------------
# Links
--------------------------------------------------------------*/
a {
  color: #0073aa;
}

a:visited {
  color: #333;
}

a:hover, a:focus, a:active {
  color: #00a0d2;
}

a:focus {
  outline: thin dotted;
}

a:hover, a:active {
  outline: 0;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 1px solid;
  border-color: #ccc #ccc #bbb;
  border-radius: 3px;
  background: #e6e6e6;
  color: rgba(0, 0, 0, 0.8);
  font-size: 12px;
  font-size: 0.75rem;
  line-height: 1;
  padding: .6em 1em .4em;
  cursor: pointer;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
  border-color: #ccc #bbb #aaa;
}

button:active, button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
  border-color: #aaa #bbb #bbb;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
  color: #111;
}

select {
  border: 1px solid #ccc;
}

textarea {
  width: 100%;
}

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/

.site-header {
  padding-bottom: 1em;
}

.header-image {
  margin: 0 auto;
}

.header-image img {
  display: block;
  margin: 0 auto;
}

.custom-logo-link {
  display: inline-block;
  margin: 0 auto 1em;
}

.site-branding {
  text-align: center;
  padding: 60px 0 1.75em;
}

.site-title {
  margin: 0 1em;
  font-family: var(--highlight-font-family);
  font-size: 38px;
  font-weight: bold;
  line-height: 1.2;
  color: #333332;
}

.site-title a {
  text-decoration: none;
  color: inherit;
}

.site-description {
  margin: .4375em 0 0;
  font-family: var(--highlight-font-family);
  font-size: 17px;
  font-weight: inherit;
  line-height: 1.4;
  word-wrap: break-word;
  color: #757575;
}

/*--------------------------------------------------------------
## Main navigation menu
--------------------------------------------------------------*/
.main-navigation {
  clear: both;
  display: block;
  margin: 0 auto 2em;
  padding: 0 1em;
  max-width: calc(var(--content-width) * 16)px; /* Fallback for older browsers. */
  max-width: var(--content-width)rem;
  font-family: var(--highlight-font-family);
}

.menu-toggle {
  display: block;
  margin: 1.2em auto;
  padding: .6em 1.2em .5em;
  font-family: var(--highlight-font-family);
  font-size: 80%;
  text-transform: uppercase;
  border: 2px solid black;
  border-radius: 0;
  background: transparent;
}

.main-navigation a {
  display: block;
  width: 100%;
  padding: .5em 1em .5em 0;
  text-decoration: none;
  color: black;
}

.main-navigation a:hover,
.main-navigation a:focus {
  text-decoration: underline;
}

.main-navigation ul {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-navigation li {
  position: relative;
}

.main-navigation ul ul li {
  padding-left: 1em;
}

.main-navigation .dropdown,
.main-navigation button.dropdown-toggle {
  display: none;
}

.main-navigation .menu {
  display: none;
}

@media (--narrow-menu-query) {
  .main-navigation.toggled-on .menu {
    display: block;
  }
}

@media (--wide-menu-query) {
  .menu-toggle {
    display: none;
  }

  .main-navigation ul li a {
    padding: .4em 0.5em;
  }

  .main-navigation ul li {
    margin: 0 0 0 0.5em;
  }
  .main-navigation ul li:first-child {
    margin-left: 0;
  }

  .main-navigation ul ul {
    display: none;
    position: absolute;
    top: 100%;
    flex-direction: column;
    background: white;
    margin-left: 0;
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
    z-index: 1;
  }

  .main-navigation ul ul a {
    width: 200px;
  }

  .main-navigation ul ul li {
    padding-left: 0;
    margin-left: 0;
  }

  .main-navigation ul ul li a {
    width: 218px;
    background: none;
  }

  .main-navigation ul ul ul {
    top: 0;
    left: 100%;
    min-height: 100%;
  }

  .main-navigation .menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .main-navigation .menu-item-has-children,
  .main-navigation .page_item_has_children {
    padding-right: var(--dropdown-symbol-width);
  }

  .main-navigation .dropdown,
  .main-navigation button.dropdown-toggle {
    display: block;
    background: transparent;
    position: absolute;
    right: 0;
    top: 50%;
    width: var(--dropdown-symbol-width);
    height: var(--dropdown-symbol-width);
    font-size: inherit;
    line-height: inherit;
    margin: 0;
    padding: 0;
    border: none;
    border-radius: 0;
    transform: translateY(-50%);
    overflow: visible;
  }

  .main-navigation .dropdown-symbol {
    display: block;
    background: transparent;
    position: absolute;
    right: 20%;
    top: 35%;
    width: 60%;
    height: 60%;
    border: solid black;
    border-width: 0 2px 2px 0;
    transform: translateY(-50%) rotate(45deg);
  }

  .main-navigation ul ul .dropdown,
  .main-navigation ul ul button.dropdown-toggle {
    top: 40%;
    right: 0.2em;
  }

  .main-navigation ul ul .dropdown-symbol {
    transform: rotate(-45deg);
  }

  /*
   * If the dropdown toggle is active with JS, then
   * we'll take care of showing the submenu with JS.
   *
   * "focus-within" is an alternative to focus class for
   * supporting browsers (all but IE/Edge) for no-JS context
   * (e.g. AMP) See https://caniuse.com/#feat=css-focus-within
   */
  .main-navigation li:hover > ul,
  .main-navigation li.toggled-on > ul,
  .main-navigation:not(.has-dropdown-toggle) li:focus > ul,
  .main-navigation:not(.has-dropdown-toggle) li:focus-within > ul {
    display: block;
  }
}


/*--------------------------------------------------------------
# Content navigation
--------------------------------------------------------------*/
.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
  border-bottom: 1px solid #111;
  margin: 0 auto 2em;
  max-width: 720px;
  overflow: hidden;
  padding-bottom: 2em;
  font-family: var(--highlight-font-family);
}

.nav-links {
  display: flex;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  width: 50%;
  flex: 1 0 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  text-align: end;
  flex: 1 0 50%;
}

.post-navigation-sub span {
  color: var(--global-font-color);
  text-decoration: none;
  display: inline-block;
}

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
.site-footer {
  margin: 1em 0 0;
  padding: 2em 0;
  font-family: var(--highlight-font-family);
}

.site-info {
  text-align: center;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
  /* Theme Footer (when set to scrolling) */
  display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
  display: block;
}
